.certificate {
    height: 575px;
    padding: 120px 0;
    &__text {
        margin-top: 48px;
        font-weight: 400;
        font-size: 16px;
        line-height: 160%;
        color: #424551;
    }
    &__wrapper {
        width: 395px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 32px;
    }
    &__image {
        position: absolute;
        right: -60px;
        top: 0;
    }
}